.form-control {
  height: auto;
  padding: 14px 5px;
  border: 1px solid #c1c1c1;
  border-radius: 0;
  font-size: 14px;
  outline: none;
}

.form-control:hover,
.form-control:focus {
  -webkit-box-shadow: none;
  -moz-box-shadow: none;
  -ms-shadow: none;
  box-shadow: none;
}

.form-control:focus {
  border-color: #000;
}

.custom-btn {
  background-color: #004a7f;
  color: #fff;
  text-align: center;
  border: none;
  padding: 15px;
}

.custom-btn:hover {
  color: #c1c1c1;
}

.custom-table {
  font-size: 14px;
  border-collapse: collapse;
}

.custom-table th,
.custom-table td {
  padding: 15px;
  min-width: 180px;
}

.custom-table th .icon,
.custom-table td .icon {
  margin-right: 3px;
}

.custom-table .tb-btn {
  border: 1px solid rgba(0, 0, 0, 0.3);
  padding: 5px 15px;
  background-color: #fff;
}

.custom-table .tb-btn .white {
  display: none;
}

.custom-table .tb-btn:hover {
  background-color: #004a7f;
  color: #fff;
}

.custom-table .tb-btn:hover .white {
  display: inline;
}

.custom-table .tb-btn:hover .black {
  display: none;
}

.custom-table .tb-btn-smpl {
  border: none;
  background: none;
  outline: none;
  padding: 0;
  margin: 0;
}

.custom-modal .modal-header {
  align-items: center;
  border-bottom: none;
}

.custom-modal .modal-header .close {
  margin: 0;
