.drag-and-drop-wrapp {
  height: calc(100% - 50px);
  margin: 25px;
  /*
  .file__input--label:after {
  	content: attr(data-text-btn);
  	border-radius: 3px;
  	background-color: #536480;
  	box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.18);
  	padding: 0.9375rem 1.0625rem;
  	margin: -0.9375rem -1.0625rem;
  	color: white;
  	cursor: pointer;
  }
  */
}

.drag-and-drop-wrapp .drop-zone {
  box-shadow: 0px 3px 24px 0px rgba(0, 0, 0, 0.14);
  width: 100%;
  height: 100%;
  padding: 10px 40px;
  display: flex;
  flex-wrap: wrap;
  text-align: center;
  cursor: pointer;
  border: 2px dashed #004a7f;
  border-radius: 20px;
  background-color: #f6f7f9;
}

.drag-and-drop-wrapp .drop-zone .drop-zone__prompt {
  width: 100%;
}

.drag-and-drop-wrapp .drop-zone h3 {
  font-size: 21px;
  font-weight: 600;
}

.drag-and-drop-wrapp .drop-zone p {
  font-size: 18px;
  max-width: 300px;
  margin: 0 auto;
}

.drag-and-drop-wrapp .drop-zone .img {
  margin: 30px auto 0;
}

.drag-and-drop-wrapp .drop-zone .link-dv {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 10px;
  display: flex;
  justify-content: center;
}

.drag-and-drop-wrapp .drop-zone .link-dv .btnUpload {
  margin-left: auto;
}

.drag-and-drop-wrapp .drop-zone--over {
  border-style: solid;
}

.drag-and-drop-wrapp .drop-zone__input {
  display: none;
}

.drag-and-drop-wrapp .drop-zone__thumb {
  width: 100%;
  height: 100%;
  border-radius: 10px;
  overflow: hidden;
  background-color: #cccccc;
  background-size: cover;
  position: relative;
}

.drag-and-drop-wrapp .drop-zone__thumb::after {
  content: attr(data-label);
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  padding: 5px 0;
  color: #ffffff;
  background: rgba(0, 0, 0, 0.75);
  font-size: 14px;
  text-align: center;
}

.drag-and-drop-wrapp .file {
  position: relative;
  width: 100%;
  font-weight: 600;
  padding-bottom: 70px;
}

.drag-and-drop-wrapp .file__input {
  position: relative;
}

.drag-and-drop-wrapp .file__input,
.drag-and-drop-wrapp .file__value {
  background-color: rgba(255, 255, 255, 0.1);
  border-radius: 3px;
  margin-bottom: 0.875rem;
  color: rgba(0, 0, 0, 0.3);
  padding: 0.9375rem 1.0625rem;
}

.drag-and-drop-wrapp .file__input--file {
  position: absolute;
  opacity: 0;
  position: absolute;
  opacity: 0;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
}

.drag-and-drop-wrapp .file__input--label {
  display: flex;
  justify-content: space-between;
  margin-bottom: 0;
  cursor: pointer;
}

.drag-and-drop-wrapp .file__value {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0;
}

.drag-and-drop-wrapp .file__value:after {
  content: "X";
  cursor: pointer;
}

.drag-and-drop-wrapp .file__remove {
  display: block;
  width: 20px;
  height: 20px;
  border: 1px solid #000;
}

.drag-and-drop-wrapp .formWrappFile {
  width: 100%;
  position: relative;
}

